<template>
    <section class="pc-container pb-4">
        <div class="pcoded-content">
            <div class="row mb-4">
                <h5 class="text-dark fw-bold">Agenda / Alertas</h5>
            </div>
            <div class="row">
                <div class="col-xl-12">
                    <div class="card shadow">
                        <div class="card-header">
                            <h5 class="text-secondary">Lista de Alertas</h5>
                        </div>
                        <div class="card-body">
                            <div class="table-responsive">
                                <table class="table" id="table" ref="myDataTable">
                                    <thead>
                                        <tr>
                                            <th class="col-md-4">N° de Expediente</th>
                                            <th class="col-md-3">Fecha de Vencimiento</th>
                                            <th class="col-md-5">Descripcion</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(alerta, index) in alertas" :key="index">
                                            <td class="col-md-4">
                                                <a href="javascript:void(0)" @click="navegarAExpediente(alerta.ale_exp_id)">
                                                    {{ alerta.ale_expediente }}
                                                </a>
                                            </td>
                                            <td class="col-md-3">{{ alerta.fecha }}</td>
                                            <td class="col-md-5">{{ alerta.ale_descripcion }}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
  
<script>
import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import feather from 'feather-icons';
import axios from 'axios';
import { showLoadingAlert, closeLoadingAlert } from '../../utils/loader';
import { handleApiError, waitForDataTableDraw } from '../../utils/utils';
import DataTable from '../../utils/dataTable';

export default {
    name: 'alertas',
    mixins: [DataTable],
    data() {
        return {
            alertas: [],
            dataTable: null,
        };
    },
    mounted() {
        this.leer();
    },
    updated() {
        feather.replace();
    },
    methods: {
        async leer() {
            try {
                showLoadingAlert();
                const headers = {
                    Authorization: `Bearer ${this.$store.getters.get__token}`,
                };
                const response = await axios.get(`${this.$store.getters.get__url}/alerta`, { headers });
                console.log(response);
                if (response.data) {
                    this.alertas = response.data.data;
                    await waitForDataTableDraw(this, 'myDataTable');
                    closeLoadingAlert();
                } else {
                    closeLoadingAlert();
                    throw new Error('Error al obtener alertas desde la API');
                }
            } catch (error) {
                handleApiError(error, 'leer');
            }
        },
        navegarAExpediente(expedienteId) {
            this.$router.push({ name: 'expediente/detalle', params: { id: expedienteId } });
        }
    },
};
</script>
  
<style scoped>
@import '../../assets/css/dataTable.css';
</style>